<template>
    <div class="" style="width: 100rem;height:30rem;">
        <div id="swipertop" class="swiper-container" style="width: 100rem;height:30rem;">
            <div class="swiper-wrapper" style="width: 100rem;height:30rem;">
                <div class="swiper-slide" style="width:100rem;" v-for="image in topimglist" :key="image.num">
                    <div class="swipertop" >
                        
                    </div>
                </div>                    
            </div>
            <div class="pagination">
                <div id="swiper-pagination-2" class="swiper-pagination swiper-pagination-clickable swiper-pagination-bullets">
                    <span class="swiper-pagination-bullet" tabindex="0" role="button" aria-label="Go to slide 1"></span>
                    <span class="swiper-pagination-bullet swiper-pagination-bullet-active" tabindex="0" role="button" aria-label="Go to slide 2"></span>
                    <span class="swiper-pagination-bullet" tabindex="0" role="button" aria-label="Go to slide 3"></span>
                    <span class="swiper-pagination-bullet" tabindex="0" role="button" aria-label="Go to slide 4"></span>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
// Used only in vue2.x version
// import vueSlider from 'vue-slider-component'
import Swiper from "swiper";
import "swiper/dist/css/swiper.min.css";

export default {
    name: 'swipertop',
    components: {
        Swiper
    },
    data: function() {
        return {
            topimglist:[
                {
                    num:1,
                    class:'swipertop'
                },
                {
                    num:2,
                    class:'swipertop'
                },
                {
                    num:3,
                    class:'swipertop'
                }
            ]
        }
    },
    mounted() {
        var mySwiper = new Swiper("swiper-container", {
            autoplay: true,
            width: 400,
            loop: true,
            pagination: {
                el: '.swiper-pagination'
            }
        });
    }
}
</script>
<style>
.swipertop {
    width:100rem;
    height:32.5rem;    
    margin: 0 auto;
    border: 0px solid #000000;
    background: 
        url(../view/image/top.png) 50% 0 no-repeat,
        url(../view/image/top_bgd.png)  repeat-x;
    background-size: contain,100%;
    background-position: 50%,0px 100%;

}
* {
    margin: 0;
    padding: 0;
    font-family: 微软雅黑;
    list-style: none;
}
#box{
    width:100rem;
    height:32.5rem;
    margin: 0px auto;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
ul{
    width: 2000px;
    animation: dh 10s infinite ease;
}
ul li{
    width:100%;
    height:32.5rem;
    float: left;

}
ul li:nth-child(1){
    width:100%;
    height:32.5rem;
    float: left;
    /* background:#4b86db; */
}
ul li:nth-child(2){
    width:100%;
    height:32.5rem;
    float: left;
    background:#ff9999;
}
ul li:nth-child(3){
    width:100%;
    height:32.5rem;
    float: left;
    background:olivedrab;
}
ul li:nth-child(4){
    width:100%;
    height:32.5rem;
    float: left;
    background:skyblue;
}
ul li:nth-child(5){
    width:100%;
    height:32.5rem;
    float: left;
    background:#4b86db;
}
 






@keyframes dh {
      0%{transform: translateX(0)}
      /* 25%{transform: translateX(-100%)}
      50%{transform: translateX(-200%)}
      75%{transform: translateX(-300%)}
      100%{transform: translateX(-400%)}  */
}
</style>




